header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header__container{
    text-align: center;
    height: 100%;
    position: relative;
}

.header__special{
    font-size: 25px;
    transition: 2.0s;
  }
.header__special:hover {
    background: linear-gradient(rgb(9, 232, 162), rgb(1, 158, 249));
    display: inline-block;
}

/*~~~~~~~~~~~~~~~~~~~~~~~~ CTA ~~~~~~~~~~~~~~~~~~~~~~~~*/
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/*~~~~~~~~~~~~~~~~~~~~~~~~ HEADER SOCIALS ~~~~~~~~~~~~~~~~~~~~~~~~*/
.header__socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}

.header__socials::before{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}

.header__socials::after{
    content: "";
    width: 1px;
    height: 2rem;
    background: var(--color-primary);
}


/*~~~~~~~~~~~~~~~~~~~~~~~~ ME ~~~~~~~~~~~~~~~~~~~~~~~~*/
.me{
    background: linear-gradient(rgb(45, 87, 201), transparent);
    width: 22rem;
    height: 100vh;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 2rem ;
}

/*~~~~~~~~~~~~~~~~~~~~~~~~ SCROLL DOWN ~~~~~~~~~~~~~~~~~~~~~~~~*/
.scroll__down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* ~~~~~~~~~~~~~~~~~~~ MEDIA QUERIES (MEDIUM DEVICE) ~~~~~~~~~~~~~~~~~~~ */
@media screen and (max-width: 1024px){
    header{
        height: 68vh;
    }
}

/* ~~~~~~~~~~~~~~~~~~~ MEDIA QUERIES (SMALL DEVICE) ~~~~~~~~~~~~~~~~~~~ */
@media screen and (max-width: 600px){
    header{
        height: 100vh;
    }

    .header__socials, .scroll__down{
        display: none;
    }
}